<template>
  <div>
    <div class="header" :class="title === looptit ? 'loophead' : ''">
      <div class="head" :class="title === looptit ? 'loophead' : ''">
        <a @click="$router.back()" class="back"></a>
        {{ title }}
      </div>
    </div>
    <div class="loop" v-if="title === looptit">
      <van-swipe vertical height="100vh" :loop="false" :show-indicators="false">
        <van-swipe-item v-for="(image, index) in images" :key="index">
          <img v-lazy="image" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="main" v-else>
      <div class="box">
        <vue-editor v-model="content" style="display: none" />
        <div
          class="contract_box ql-editor "
          style="width: 100%"
          v-html="content"
        ></div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
import Fetch from '../../utils/fetch';
import { NavBar } from 'vant';
import { VueEditor } from 'vue2-editor';
import { Swipe, SwipeItem } from 'vant';

Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(NavBar);

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      title: '',
      content: '',
      looptit: this.$t('lang.looptit'),
      current: 0,
      images: [],
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    Fetch('/article/info/detail', {
      code: this.$router.history.current.params.code,
    }).then(res => {
      this.title = res.data.title;
      this.content = res.data.content;
      if (this.title === this.$t('lang.looptit')) {
        let str = this.content.replace('<p>', '').replace('</p>', '');
        var matchReg = /upload.*?\.png/gi;
        var arr = str.match(matchReg);
        arr.forEach(item => {
          this.images.push('/' + item);
        });
      }
    });
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    back() {
      this.$router.back();
    },
  },
};
</script>

